<template>
  <div class="v-main-wrapper">
    <v-login v-if="!isAuthenticated"/>
    <div v-else class="wrapper">
      <v-navbar :menuItems="menuItems" />      
      <main class="main">
        <v-header />
        <div class="main__wrapper">
          <router-view />
        </div>
      </main>
    </div>

  </div>  
</template>

<script>
import {mapState} from 'vuex'

import vLogin from './login/v-login'
import vHeader from './layouts/v-header'
import vNavbar from './layouts/navbar/v-navbar'

export default {
  name: 'v-main-wrapper',
  components: {
    vLogin,
    vHeader,
    vNavbar
  },
  data: () => ({
    menuItems: [
      { name: 'Главная', icon: 'home', to: '/' },
      { name: 'Эффективности', icon: 'grading', to: '/efficiency', dropdownMenu: [
        { name: 'Блок 1', to: '/efficiency/1' },
        { name: 'Блок 2.1', to: '/efficiency/2-1' },
        { name: 'Блок 2.2', to: '/efficiency/2-2' },
        { name: 'Блок 3', to: '/efficiency/3' },
      ] },
      { name: 'Стимулирования', icon: 'grading', to: '/incentives', dropdownMenu: [
        { name: 'Блок 1', to: '/incentives' },
        { name: 'Блок 2', to: '/incentives' }
      ] },
      { name: 'Нормативные документы', icon: 'content_paste', to: '/normative' },
      { name: 'Архив', icon: 'folder', to: '/archive' },
      { name: 'Настройки', icon: 'settings', to: '/settings' }
    ]
  }),
  computed: {
    ...mapState(['user']),
    isAuthenticated() {
      return !!this.user
    }
  }
}
</script>

<style lang="scss">
.wrapper {
  height: 100vh;
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.main {
  padding: 0 2rem;
  width: 80%;
  background-color: #424568;
  &__wrapper {
    font-size: 2rem;
    margin-top: 2rem;
  }
}
</style>