<template>
  <li 
    class="v-navbar-item"
    @click="toggleDropdown"
  >
    <router-link 
      :active-class="'active'"
      exact :to="to"
    >
      <i class="material-icons">{{icon}}</i>
      {{name}}
    </router-link>
    <ul 
      class="v-navbar-item__dropdown"
      v-if="existDropdown && isDropdownVisible"
    >
      <li v-for="(item, idx) in dropdownMenu" :key="idx">
        <router-link exact :to="item.to">
          {{item.name}}
        </router-link>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  name: 'v-navbar-item',
  props: {
    name: {
      type: String,
      default: 'item'
    },
    icon: {
      type: String,
      default: ''
    },
    to: {
      type: String,
      default: '/'
    },
    dropdownMenu: {
      type: Array,
      default: () => []
    }
  },
  data: () => ({
    isDropdownVisible: false
  }),
  computed: {
    existDropdown() {
      return this.dropdownMenu.length ? true : false
    }
  },
  methods: {
    toggleDropdown() {
      this.isDropdownVisible = !this.isDropdownVisible
    }
  }
}
</script>

<style lang="scss">
.v-navbar-item {
  margin-bottom: 1.5rem;
  &__dropdown {
    list-style: none;
    padding: .7rem 0 0 3.5rem;
    li {
      margin-bottom: 1rem;
      &:hover {
        >a, i {
          color: #fff;
        }
      }
    }
  }
  a {
    display: flex;
    align-items: center;
    color: #61647b;
    text-decoration: none;
    transition: all .3s;
    &.active {
      color: #fff;
    }
  }
  i {
    font-size: 1.7rem;
    margin-right: .5rem;
  }
  &:hover {
    >a, i {
      color: #fff;
    }
  }
}
</style>