<template>
  <section class="v-login">
    <div class="backdrop">
      
      <div class="v-login__inner">
        <div class="container">
          <nav class="nav login__nav">
            <h1 class="nav__title">
              Показатели эффективности и стимулирования <br>
              профессорско-педагогического работника
            </h1>
            <!-- nav -->
            <ul class="nav__link-list">
              <li 
                class="nav__link" 
                :class="{active: activeLink === 'contacts'}" 
                @click="changeLinkHandler('contacts')">
                Контакты
              </li>
              <li 
                class="nav__link" 
                :class="{active: activeLink === 'login'}" 
                @click="changeLinkHandler('login')">
                <div class="nav__link-icon">
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                    <path fill-rule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clip-rule="evenodd" />
                  </svg>
                </div>
                Войти в систему
              </li>
            </ul>
          </nav>
        </div>

        <!-- form -->
        <transition name="fade">
          <div class="v-login-form v-login__form" v-if="activeLink === 'login'">
            <form class="v-login-form__wrapper" @submit.prevent="signIn">
              <div class="el-input">
                <label for="email">Введите Email адрес</label>
                <input type="email" v-model="email" placeholder="Email" id="email">
              </div>
              <div class="el-input">
                <label for="password">Введите пароль</label>
                <input type="password" v-model="password" placeholder="Пароль" id="password">
              </div>
              <button class="v-login-form__submit" type="submit">
                Войти
                <div class="v-login-form__submit-icon">
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 9l3 3m0 0l-3 3m3-3H8m13 0a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
              </button>
              <div class="v-login-form__footer">
                <a href="#" class="v-login-form__forgot">Забыли пароль?</a>
                <div class="v-login-form__remember">
                  <label for="remember">Не выходить из системы</label>
                  <input type="checkbox" id="remember">
                </div>
              </div>
            </form>
          </div>
        </transition>

      </div>
    </div>
  </section>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  name: 'v-login',
  components: {

  },
  data: () => ({
    activeLink: '',

    email: '',
    password: '',
  }),
  methods: {
    ...mapActions(['SIGN_IN']),
    signIn() {
      const formData = {
        email: this.email,
        password: this.password
      }

      this.SIGN_IN(formData)
    },
    logout() {
    },
    changeLinkHandler(v) {
      this.activeLink = v
    }
  }
}
</script>

<style lang="scss">
.v-login {
  height: 100vh;
  max-height: 100vh;
  background: url('../../assets/login-bg.jpg') no-repeat center / cover;
  overflow: hidden;
  &__inner {
    position: relative;
    height: 100%;
    padding: 1em 0;
  }
  &__nav {
    margin-top: 4em;
  }
  &__form {
    position: absolute;
    right: 1em;
    top: 1em;
    bottom: 1em;
    width: 45%;
    border-radius: 1em;
  }
}

.nav {
  &__title {
    color: #fff;
    font-size: 1.4em;
  }
  &__link-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 1em 1.5em;
    margin-right: 1.3em;
    border-radius: .7em;
    background-color: rgba(255, 255, 255, .25);
    color: #fff;
    cursor: pointer;
    transition: .3s;
    &.active {
      background-color: #fff;
      color: #000;
    }
    &-icon {
      margin-right: .3em;
      width: 24px;
      height: auto;
    }
  }
}

.fade-enter-active, .fade-leave-active {
  transition: transform .7s;
}

.fade-enter, .fade-leave-to {
  transform: translateX(100%);
}

.v-login-form {
  padding: 10em 5em;
  background-color: #fff;
  &__wrapper {
    width: 100%;
  }
  &__submit {
    width: 10em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .5em;
    padding: 0.7em 1em 0.7em 3em;
    font-size: 1.3em;
    line-height: 1em;
    background-color: #ccc;
    border: none;
    border-radius: 1em;
    outline: none;
    background-color: #1a204f;
    color: #fff;
    cursor: pointer;
    transition: .3s;
    &:hover {
      opacity: .9;
    }
    &-icon {
      margin-left: .3em;
      width: 26px;
      height: auto;
    }
  }
  &__footer {
    margin-top: 1.5em;
    display: flex;
    justify-content: space-between;
    font-size: 1.2em;
  }
  &__forgot {
    color: #1a204f;
  }
  &__remember {
    display: flex;
    align-items: center;
    label {
      cursor: pointer;
      user-select: none;
    }
    input[type=checkbox] {
      margin-left: .5em;
      width: 1.5em;
      height: 1.5em;
      cursor: pointer;
    }
  }
}

.el-input {
  margin-bottom: .7em;
  display: flex;
  flex-direction: column;
  label {
    margin-bottom: .4em;
    font-size: 1.3em;
  }
  input {
    width: 70%;
    padding: 1.3em;
    background-color: #f5f6f9;
    border: none;
    border-radius: 1em;
    outline: none;
    &::placeholder {
      font-size: 1.1em;
    }
    &:focus {
      background-color: #406b8a;
      color: #fff;
      &::placeholder {
        color: #fff;
      }
    }
  }
}
</style>